.container {
  @apply h-screen 
  flex flex-col justify-center items-center w-full px-4;
}

.title {
  @apply bg-secondary-base rounded-lg
  px-6 py-2 mb-2
  border-4 border-secondary-light
  text-secondary-dark text-2xl;
}

.question {
  @apply w-full flex flex-col justify-center items-end
    p-4 mx-4
    bg-primary-base
  border-8  border-primary-dark
    rounded-xl
    text-primary-light text-2xl text-center;
}

.thankYou {
  @apply w-full 
    py-8 mx-4
    bg-primary-base
  border-8  border-primary-dark
    rounded-xl
    text-primary-light text-2xl text-center;
}

.question p {
  @apply mb-4;
}

.answer {
  @apply bg-primary-sky text-primary-light
   my-2 px-4
   rounded-md border-2 border-smoke;
}

.answer {
  @apply bg-primary-sky text-primary-light
  text-right
   w-3/4 min-w-fit my-2 px-4 py-2
   rounded-md border-2 border-smoke;
}

.answer:hover {
  @apply bg-primary-light text-primary-sky;
}

.id {
  @apply text-primary-sky text-base;
}

.trapezoid {
  border-bottom: 30px solid transparent;
  border-top: 30px solid transparent;
  border-left: 180px solid #66b2ff;
  height: 70px;
}
