.container {
  @apply flex flex-col h-screen
    justify-center items-center w-full px-4 text-3xl;
}

.item {
  @apply w-1/2
  flex flex-row-reverse items-center
    my-2 bg-primary-sky rounded-3xl px-12 py-3;
}

.number {
  @apply bg-secondary-base text-secondary-dark rounded-full mx-4 p-3 px-6 text-center;
}

.name {
  @apply text-primary-dark;
}
