.overlay {
  @apply flex flex-col fixed inset-0 w-screen h-screen bg-black bg-opacity-50 z-50 justify-center;
}

.dialog {
  @apply bg-gray-700  w-2/5
   /* w-96 min-w-fit max-w-full  */
  rounded-xl mx-auto shadow-lg;
}

.content {
  @apply py-4 px-3;
}

.title {
  @apply font-medium text-lg text-gray-300 mb-2;
}

.description {
  @apply text-sm text-gray-400 mb-4;
}

.hr {
  @apply border-gray-600 mx-4;
}

.footer {
  @apply flex justify-end py-4 px-2;
}

.footer button {
  @apply text-sm font-medium w-fit mx-2 rounded-xl select-none  py-3 px-4;
}

.footer button:focus {
  @apply outline-none  ring-4 ring-opacity-50;
}

.normal {
  @apply text-gray-400;
}

.normal:hover {
  @apply text-gray-100;
}

.normal:focus {
  @apply ring-gray-600;
}

.primary {
  @apply bg-orange
  text-orange;
}

.primary:hover {
  @apply bg-orange 
  text-orange;
}

.primary:focus {
  @apply ring-orange;
}

.danger {
  @apply bg-red-300
  text-red-600;
}

.danger:hover {
  @apply bg-red-400 
  text-red-800;
}

.danger:focus {
  @apply ring-red-600;
}
