.container {
  @apply h-screen 
   flex flex-col justify-center items-center;
}

.wrapper {
  @apply flex flex-col justify-center items-stretch w-full;
  max-width: 560px;
}

.prompt {
  @apply text-white text-lg tracking-wider text-center px-4;
}

.textInput {
  @apply py-8 px-6 w-full;
}

.textInput input {
  @apply rounded-lg w-full border-4 border-gray-300  bg-transparent 
  py-4 px-4 text-right
  text-white text-lg;
}

.readyBtn {
  @apply text-center;
}

.readyBtn button {
  @apply py-6 px-14 bg-orange rounded-lg text-white text-xl;
}

.readyBtn button:hover {
  @apply bg-opacity-80;
}
